import React from "react"
import { graphql } from "gatsby"
import {Trans, useTranslation} from "gatsby-plugin-react-i18next"
import Layout from "../components/Layout"

const Portfolio = ({data}) => {

  const projects = data.site.siteMetadata.projects

  const {t} = useTranslation(['header', 'portfolio']) 

  return (
    <Layout page="PortfolioPage" title={t("header:PORTFOLIO","PORTFOLIO")}>
      <TimeLine projects={projects} t={t}/>
    </Layout>
  )
}

const TimeLine = ({ projects, t }) => (
  <main id="portfolio">
    <section className="timeline">
      {projects.map(project => (
        <div className={project.className} key={project.name}>
          <div className="content">
            <h2>
              {project.name} ({t(`portfolio:projects.${project.name.toLowerCase()}.date`, project.date)})
            </h2>
            <p>{t(`portfolio:projects.${project.name.toLowerCase()}.desc`, project.desc)}</p>
          </div>
        </div>
      ))}
    </section>
  </main>
)

export const portfolioQuery = graphql`
  query {
    site {
      siteMetadata {
        projects {
          name
          date
          desc
          className
        }
      }
    }
  }
`

export default Portfolio
